<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-primary mt-4" @click="Openmodal(true)"></button>
      <!-- <button class="btn btn-primary mt-4" data-toggle="modal" data-target="#productModal"></button> -->
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{item.title}}</td>
          <td>{{item.code}}</td>
          <td>{{item.percent}}</td>
          <td>{{item.due_date}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="Openmodal(false,item)"></button>
            <button class="btn btn-outline-danger btn-sm" @click="Opendeletemodal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination v-if="pagination.total_pages>1" :page-data="pagination" @pagemove="getCoupons"></Pagination>
    <!-- add&edit Modal -->
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempCoupon.title"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <label for="code"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="code"
                    v-model="tempCoupon.code"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <label for="dueDate"></label>
                  <input
                    type="date"
                    class="form-control"
                    id="dueDate"
                    v-model="tempCoupon.due_date"
                  >
                </div>
                <div class="form-group">
                  <label for="dueDate"></label>
                  <select name class="form-control mt-3" v-model="tempCoupon.percent">
                    <option :value="num" v-for="num in 100" :key="num">{{num}}%</option>
                  </select>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      v-model="tempCoupon.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      id="is_enabled"
                    >
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- add&edit Modal -->
    <!-- delete Modal -->
    <div
      class="modal fade"
      id="delCouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteCoupon"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- delete Modal -->
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  data() {
    return {
      coupons: [],
      pagination: {},
      tempCoupon: {},
      isNew: false,
      isLoading: false, // trueloading
    };
  },
  methods: {
    getCoupons(page = 1) {
      // page = 1
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log('getCoupons()', response.data);
        vm.isLoading = false;
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
      })
    },
    Openmodal(isNew, item) {
      if (isNew) {
        this.tempCoupon = {};
        this.isNew = true;
      } else {
        this.tempCoupon = Object.assign({}, item);// itemthis.tempCoupon=item
        this.isNew = false;
      }
      $('#couponModal').modal('show');
    },
    updateCoupon() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/admin/coupon`;
      let httpMethod = 'post';
      const vm = this;
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = 'put';
      }
      this.$http[httpMethod](api, { data: vm.tempCoupon }).then(response => {
        console.log(response.data);
        if (response.data.success) {
          $('#couponModal').modal('hide');
          vm.getCoupons(vm.pagination.current_page);
        } else {
          $('#couponModal').modal('hide');
          vm.getCoupons();
          console.log('');
        }
      })
    },
    Opendeletemodal(item) {
      this.tempCoupon = Object.assign({}, item);// itemthis.tempCoupon=item
      $('#delCouponModal').modal('show');
    },
    deleteCoupon() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/admin/coupon/${vm.tempCoupon.id}`;
      this.$http.delete(api).then(response => {
        if (response.data.success) {
          vm.getCoupons();
          console.log('');
        } else {
          vm.getCoupons();
          console.log('');
        }
        $('#delCouponModal').modal('hide');
      })
    },
  },
  created() {
    this.getCoupons();
  }
}
</script>



